<template>
  <div class="row mt-3">
    <div class="col-lg-12 col-md-12">
      <div class="card">
        <div class="card-body">
          <img class="mx-auto d-block" src="../../src/assets/icon.png" alt />
          <form @submit.prevent="onSubmit">
            <div class="form-group">
              <label for="email">邮箱</label>
              <input type="email" class="form-control" v-model="email" />
            </div>
            <div class="form-group">
              <label for="password">密码</label>
              <input type="password" class="form-control" v-model="password" />
            </div>
            <button type="submit" class="btn btn-block btn-success">登录</button>
          </form>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import axios from "axios";
export default {
  data() {
    return {
      email: "",
      password: ""
    };
  },
  beforeRouteEnter (to, from, next) {
    next(vm => vm.$store.dispatch('changeStatus',null))
  },
  methods: {
    onSubmit() {
      axios.get("users.json").then(res => {
        let dataList = res.data;
        const arr = [];
        for (let index in dataList) {
          arr.push(dataList[index]);
        }
        let result = arr.filter(item => {
          return item.email === this.email && item.password === this.password;
        });
        if (result != null && result.length > 0) {
          this.$router.push({name:'Home'});
          this.$store.dispatch('changeStatus',result[0].email);
        } else {
          alert("用户名不存或用户名密码错误，请核对后重新输入！");
          this.$store.dispatch('changeStatus',null);
        }
      });
    }
  }
};
</script>

